import React, { Component } from 'react';
import { Subscription } from 'rxjs';
import { LiquidityAssetType } from '../../models/Enums';
import { EthereumService } from '../../services/EthereumService';
import { Utils } from '../../services/Utils';
import { ILiquidityAssetService } from './Services/Interfaces';
import { BalancerStakeAssetService } from './Services/BalancerStakeAssetService';
import { LpLandAssetService } from './Services/LpLandAssetService';
import { MessagingService } from '../../services/MessagingService';

interface IOpportunitiesProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface IOpportunitiesState {
  liquidityAssetService?: ILiquidityAssetService | null;
  addAmount: string | number;
  addBalance: string | number;
  liquidityAssetServices: ILiquidityAssetService[];
  showDetail: boolean;
}

class Opportunities extends Component<IOpportunitiesProps, IOpportunitiesState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;

  constructor(props: any) {
    super(props);

    this.state = {
      liquidityAssetService: null,
      addAmount: 0,
      addBalance: 0,
      liquidityAssetServices: [],
      showDetail: false
    };

    this.displayInvestment = this.displayInvestment.bind(this);
    this.cancelAdd = this.cancelAdd.bind(this);
    this.setAmount = this.setAmount.bind(this);
    this.setPercentAmount = this.setPercentAmount.bind(this);
    this.approve = this.approve.bind(this);
    this.confirmAdd = this.confirmAdd.bind(this);

    this.subscription = new Subscription();
  }

  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });

    this.subscription.add(eventSubscription);

    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  refresh() {
    if (
      this.props.ethereumservice.walletData.walletAddress &&
      this.props.ethereumservice.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumservice.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumservice.networkManager!.network!?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumservice.walletData.walletAddress;
        this.chainId = this.props.ethereumservice.networkManager!.network!.chainId;
        this.init();
      } else {
        this.update();
      }
    }
  }

  init() {
    if (
      this.props.ethereumservice.networkManager?.network &&
      this.props.ethereumservice.networkManager?.network?.liquidityConfigs.length > 0
    ) {
      var liquidityUpdatePromises = this.props.ethereumservice.networkManager!.network!.liquidityConfigs.map(lc => {
        switch (lc.liquidityType) {
          case LiquidityAssetType.BalancerStake:
            var liquidityAssetService = new BalancerStakeAssetService(
              lc,
              this.props.ethereumservice
            ) as ILiquidityAssetService;
            break;
          case LiquidityAssetType.wLand:
            liquidityAssetService = new LpLandAssetService(lc, this.props.ethereumservice) as ILiquidityAssetService;
            break;
          default:
            throw new Error('Unknown LiquidityAssetType');
        }

        return liquidityAssetService.update().then(() => {
          return liquidityAssetService;
        });
      });

      return Promise.all(liquidityUpdatePromises).then(liquidityAssetServices => {
        this.setState({ liquidityAssetServices: liquidityAssetServices });
      });
    }
  }

  update() {
    this.state.liquidityAssetServices.forEach(liquidityAsset => {
      liquidityAsset.update();
    });

    this.setState({ liquidityAssetServices: this.state.liquidityAssetServices });
  }

  displayInvestment(liquidityAssetService: ILiquidityAssetService) {
    var lptBalance =
      liquidityAssetService.approvedLptBalance <= 0 &&
      liquidityAssetService.lptBalance > liquidityAssetService.approvedLptBalance
        ? liquidityAssetService.lptBalance
        : liquidityAssetService.approvedLptBalance;

    this.setState({ liquidityAssetService: liquidityAssetService, addAmount: 0, addBalance: lptBalance });
  }

  cancelAdd() {
    this.setState({ liquidityAssetService: null, addAmount: 0 });
  }

  setAmount(e: any) {
    this.setState({ addAmount: e.target.value });
  }

  setPercentAmount(percentage: number) {
    var addAmount = Utils.formatUnits(Utils.parseUnits(this.state.addBalance).mul(percentage).div(100));
    this.setState({ addAmount: addAmount });
  }

  approve() {
    this.state.liquidityAssetService!.approve(this.state.addAmount);
  }

  confirmAdd() {
    this.state.liquidityAssetService!.add(this.state.addAmount);
  }

  render() {
    return (
      <React.Fragment>
        <div className="flex">
          {!this.state.liquidityAssetService && <div className="content">{this.renderOpportunities()}</div>}
          {this.state.liquidityAssetService && <div className="narrow-content">{this.renderInvest()}</div>}
        </div>
      </React.Fragment>
    );
  }

  renderOpportunities() {
    return (
      <div>
        <div className="table-control">
          <table className="table is-hoverable is-fullwidth">
            <thead>
              <tr>
                <th></th>
                <th>Available</th>
                <th>Liquidity</th>
                <th>ROI</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {/* TODO add filter and message if there aren't any opps
              .filter(o => o.lptBalance > 0) */}
              {this.state.liquidityAssetServices.map(la => (
                <OpportunityRow
                  key={la.config.poolContractAddress}
                  onAddClick={this.displayInvestment}
                  liquidityAssetService={la}></OpportunityRow>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }

  renderInvest() {
    return (
      <div className="mb-3">
        {this.state.liquidityAssetService!.lptBalance <= 0 && (
          <ul className="action-control mt-5 is-vertical">
            <li className="is-center  is-fullwidth m-0">
              <h2 className="info">
                {this.state.liquidityAssetService!.config.pool} tokens are required to{' '}
                {this.state.liquidityAssetService!.actionDisplayName}, obtain some{' '}
                {this.state.liquidityAssetService!.config.lpToken} first in order to continue
              </h2>
            </li>

            <li>
              <button className="button is-small" onClick={this.cancelAdd}>
                OK{' '}
              </button>
            </li>
          </ul>
        )}

        {this.state.liquidityAssetService!.lptBalance > 0 && this.state.liquidityAssetService!.approvedLptBalance <= 0 && (
          <React.Fragment>
            <div className="info-control">
              <h2 className="info is-center">{this.state.liquidityAssetService!.config.lpToken} :</h2>
              <h2 className="value is-center">
                <span className="pre">{Utils.toFixed(this.state.liquidityAssetService!.approvedLptBalance, 8)}</span>
                {this.state.liquidityAssetService!.approvedLptBalance <
                  this.state.liquidityAssetService!.lptBalance && (
                  <span className="sub">
                    (from {Utils.toFixed(this.state.liquidityAssetService!.lptBalance, 8)} available)
                  </span>
                )}
              </h2>
            </div>

            {this.renderApproveInvestmentAmount()}

            <ul className="action-control mt-2">
              <li>
                <button className="button is-small" onClick={this.cancelAdd}>
                  Cancel{' '}
                </button>
              </li>

              <li>
                <button
                  className="button is-danger is-small"
                  disabled={this.state.addAmount <= 0}
                  onClick={this.approve}>
                  Approve Spend{' '}
                </button>
              </li>
            </ul>
          </React.Fragment>
        )}

        {this.state.liquidityAssetService!.lptBalance > 0 && this.state.liquidityAssetService!.approvedLptBalance > 0 && (
          <React.Fragment>
            <div className="info-control">
              <h2 className="info is-center">{this.state.liquidityAssetService!.config.lpToken} :</h2>
              <h2 className="value is-center">
                <span className="pre">{Utils.toFixed(this.state.liquidityAssetService!.approvedLptBalance, 8)}</span>
                {this.state.liquidityAssetService!.approvedLptBalance <
                  this.state.liquidityAssetService!.lptBalance && (
                  <span className="sub">
                    (from {Utils.toFixed(this.state.liquidityAssetService!.lptBalance, 8)} available)
                  </span>
                )}
              </h2>
            </div>

            {this.renderAddAmount()}

            <ul className="action-control mt-5">
              <li>
                <button className="button is-small" onClick={this.cancelAdd}>
                  Cancel
                </button>
              </li>

              <li>
                <button
                  className="button is-success is-small"
                  disabled={this.state.addAmount <= 0}
                  onClick={this.confirmAdd}>
                  {this.state.liquidityAssetService!.actionDisplayName}
                </button>
              </li>
            </ul>
          </React.Fragment>
        )}
      </div>
    );
  }

  renderApproveInvestmentAmount() {
    return (
      <ul className="action-control mt-5">
        <li className="action-form">
          <div className="field has-addons">
            {this.renderInvestPercentButtons()}

            <div className="control input-control">
              <input
                className="input is-success is-small"
                type="text"
                placeholder="BPT Amount"
                value={this.state.addAmount}
                onChange={this.setAmount}
                disabled={this.state.liquidityAssetService!.lptBalance <= 0}
              />
            </div>
          </div>
        </li>
      </ul>
    );
  }

  renderAddAmount() {
    return (
      <ul className="action-control mt-5 is-vertical">
        <li className="is-center  is-fullwidth m-0">{this.renderInvestPercentButtons()}</li>

        <li className="is-center is-fullwidth m-0">
          <div className="control input-control">
            <input
              className="input is-primary is-small"
              type="text"
              placeholder="BPT Amount"
              value={this.state.addAmount}
              onChange={this.setAmount}
              disabled={this.state.liquidityAssetService!.approvedLptBalance <= 0}
            />
          </div>
        </li>
      </ul>
    );
  }

  renderInvestPercentButtons() {
    return (
      <div className="buttons has-addons">
        <button className="button is-white is-small" onClick={() => this.setPercentAmount(25)}>
          25 %
        </button>

        <button className="button is-gray is-small" onClick={() => this.setPercentAmount(50)}>
          50 %
        </button>

        <button className="button is-darkgray is-small" onClick={() => this.setPercentAmount(75)}>
          75 %
        </button>

        <button className="button is-darkergray is-small" onClick={() => this.setPercentAmount(100)}>
          100 %
        </button>
      </div>
    );
  }
}

interface IOpportunityRowProps {
  liquidityAssetService?: ILiquidityAssetService;
  onAddClick: (liquidityAssetService: ILiquidityAssetService) => void;
}

class OpportunityRow extends Component<IOpportunityRowProps> {
  add = () => {
    this.props.onAddClick(this.props.liquidityAssetService!);
  };

  render() {
    return (
      <tr>
        <td>
          <div className="flex">
            <div className="tLogo">
              <div className="tLogo_group">
                <img
                  alt={this.props.liquidityAssetService!.config.token1}
                  className="token"
                  src={this.props.liquidityAssetService!.config.token1Icon}
                />
                <img
                  alt={this.props.liquidityAssetService!.config.token2}
                  className="token"
                  src={this.props.liquidityAssetService!.config.token2Icon}
                />
              </div>
              <img
                alt={this.props.liquidityAssetService!.config.pool}
                className="tExtra"
                src={this.props.liquidityAssetService!.config.poolIcon}
              />
            </div>
            <div>
              <div className="cell_PrimaryText">
                {this.props.liquidityAssetService!.config.token1} / {this.props.liquidityAssetService!.config.token2}
              </div>
              <div className="cell_SecondaryText">{this.props.liquidityAssetService!.config.pool}</div>
            </div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">Available</div>
            <div className="cell_PrimaryText">
              {Utils.toFixed(this.props.liquidityAssetService!.lptBalance, 8)}{' '}
              {this.props.liquidityAssetService!.config.lpToken}
            </div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">Liquidity</div>
            <div className="cell_PrimaryText">${this.props.liquidityAssetService!.totalLiquidity}</div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">ROI</div>
            <div className="cell_PrimaryText">{Utils.toFixed(this.props.liquidityAssetService!.roi, 2)}% (1y)</div>
            <div className="cell_SecondaryText">
              {Utils.toFixed(this.props.liquidityAssetService!.roi / 52, 2)}% (1w)
            </div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_Action is-pulled-right">
              <button className="button is-success" onClick={this.add}>
                {this.props.liquidityAssetService!.approvedLptBalance <= 0 && <span>Approve</span>}
                {this.props.liquidityAssetService!.approvedLptBalance > 0 && (
                  <span>{this.props.liquidityAssetService!.actionDisplayName}</span>
                )}
              </button>
            </div>
          </div>
        </td>
      </tr>
    );
  }
}

export default Opportunities;
