import { Component } from 'react';
import FungibleAssets from './FungibleAssets';
import NonFungibleAssets from './NonFungibleAssets';
import Opportunities from './Opportunities';
import { EthereumService } from '../../services/EthereumService';

import wallet from '../../assets/wallet.png';
import spade from '../../assets/spade.png';
import opportunities from '../../assets/opportunities.png';

import '../Article.scss';
import './Estate.scss';
import { MessagingService } from '../../services/MessagingService';

interface IEstateProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface IEstateState {
  activeTab: string;
}

class Estate extends Component<IEstateProps, IEstateState> {
  constructor(props: IEstateProps) {
    super(props);
    this.state = {
      activeTab: 'Assets'
    };

    this.setActiveTab = this.setActiveTab.bind(this);
  }

  setActiveTab(tab: any) {
    this.setState({ activeTab: tab });
  }

  renderActiveTabContent() {
    switch (this.state.activeTab) {
      case 'Assets':
        return (
          <FungibleAssets
            messagingService={this.props.messagingService}
            ethereumservice={this.props.ethereumservice}></FungibleAssets>
        );
      case 'Collectibles':
        return (
          <NonFungibleAssets
            messagingService={this.props.messagingService}
            ethereumservice={this.props.ethereumservice}></NonFungibleAssets>
        );
      case 'Opportunities':
        return (
          <Opportunities
            messagingService={this.props.messagingService}
            ethereumservice={this.props.ethereumservice}></Opportunities>
        );
      default:
        throw new Error('tab');
    }
  }

  render() {
    return (
      <section id="wallet" className="is-vcentered">
        <div className="hero-body">
          <div className="container is-max-widescreen">
            {!this.props.ethereumservice.walletData.isConnected && (
              <div className="tile is-parent is-center">
                <article
                  className="is-child box has-background-light is-button is-center"
                  onClick={() => this.props.ethereumservice.connectWallet()}>
                  <div className="content px-3">
                    <h2 className="mb-0">Connect Wallet</h2>
                  </div>
                </article>
              </div>
            )}
            {this.props.ethereumservice.walletData.isConnected && (
              <div className="tile is-ancestor">
                <div className="tile is-vertical is-12">
                  <div className="tile is-parent">
                    <article className="card tile is-child box has-background-light">
                      <div className="title">
                        <div className="tabs is-fullwidth is-medium is-boxed">
                          <ul>
                            <li
                              className={'tab ' + (this.state.activeTab === 'Assets' ? 'is-active' : '')}
                              onClick={() => this.setActiveTab('Assets')}>
                              <a>
                                <img alt="Assets" className="token" src={wallet} />
                                <span className="tab-title">Assets</span>
                              </a>
                            </li>
                            <li
                              className={'tab ' + (this.state.activeTab === 'Collectibles' ? 'is-active' : '')}
                              onClick={() => this.setActiveTab('Collectibles')}>
                              <a>
                                <img alt="Collectibles" className="token" src={spade} />
                                <span className="tab-title">Collectibles</span>
                              </a>
                            </li>
                            <li
                              className={'tab ' + (this.state.activeTab === 'Opportunities' ? 'is-active' : '')}
                              onClick={() => this.setActiveTab('Opportunities')}>
                              <a>
                                <img alt="Opportunities" className="token" src={opportunities} />
                                <span className="tab-title">Opportunities</span>
                              </a>
                            </li>
                          </ul>
                        </div>
                      </div>
                      {this.renderActiveTabContent()}
                    </article>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </section>
    );
  }
}

export default Estate;
