import React, { Component } from 'react';
import { Subscription } from 'rxjs';
import { AssetType, IAssetConfig } from '../../models/AssetConfig';
import { INftDetails } from '../../models/NftDetails';
import { EthereumService } from '../../services/EthereumService';
import { MessagingService } from '../../services/MessagingService';
import { NonFungibleAssetService } from './Services/NonFungibleAssetService';

interface INonFungibleProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface INonFungibleState {
  investAsset?: string;
  assets: NonFungibleAssetService[];
  showDetail: boolean;
}

class NonFungibleAssets extends Component<INonFungibleProps, INonFungibleState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;
  displayAssetTypes = [AssetType.Collectible];
  equipTargetAssetsTypes = [AssetType.Crop];
  equipTarget?: IAssetConfig;

  constructor(props: any) {
    super(props);

    this.state = {
      investAsset: '',
      assets: [],
      showDetail: false
    };

    this.subscription = new Subscription();
  }

  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });

    this.subscription.add(eventSubscription);
    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  refresh() {
    if (
      this.props.ethereumservice.walletData.walletAddress &&
      this.props.ethereumservice.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumservice.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumservice.networkManager!.network?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumservice.walletData.walletAddress;
        this.chainId = this.props.ethereumservice.networkManager!.network!.chainId;
        this.init();
      } else {
        this.update();
      }
    }
  }

  init() {
    if (
      this.props.ethereumservice.networkManager?.network &&
      this.props.ethereumservice.networkManager?.network?.assets.size > 0
    ) {
      let displayAssets = [...this.props.ethereumservice.networkManager!.network!.assets.values()].filter(config =>
        this.displayAssetTypes.includes(config.assetConstants!.assetType!)
      );

      let equipTargets = [...this.props.ethereumservice.networkManager!.network!.assets.values()].filter(config =>
        this.equipTargetAssetsTypes.includes(config.assetConstants!.assetType!)
      );

      if (equipTargets.length > 1) {
        throw new Error('Multiple crops not supported for equipping collectibles');
      }
      this.equipTarget = equipTargets[0];

      var assetUpdatePromises = displayAssets.map(ac => {
        var asset = new NonFungibleAssetService(
          ac,
          this.equipTarget!,
          this.props.messagingService,
          this.props.ethereumservice
        );
        return asset.update().then(() => {
          return asset;
        });
      });

      return Promise.all(assetUpdatePromises).then(assets => {
        this.setState({ assets: assets });
      });
    }
  }

  update() {
    this.state.assets.forEach(asset => {
      asset.update();
    });

    this.setState({ assets: this.state.assets });
  }

  render() {
    return (
      <React.Fragment>{!this.state.showDetail && <div className="content">{this.renderAssets()}</div>}</React.Fragment>
    );
  }

  renderAssets() {
    return (
      <div>
        <div className="table-control">
          <table className="table is-hoverable is-fullwidth">
            <thead>
              <tr>
                <th></th>
                <th>Description</th>
                <th>Attributes</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {/* TODO add filter and message if there aren't any opps
              .filter(o => o.lptBalance > 0) */}

              {this.state.assets.map(a => (
                <EquipmentSet
                  key={a.config.contractAddress}
                  equipTarget={this.equipTarget!}
                  nftService={a}></EquipmentSet>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

interface IEquipmentSetProps {
  nftService: NonFungibleAssetService;
  equipTarget: IAssetConfig;
}

class EquipmentSet extends Component<IEquipmentSetProps> {
  constructor(props: any) {
    super(props);

    this.equip = this.equip.bind(this);
  }

  async equip(nft: INftDetails) {
    if (this.props.nftService.isApproved) {
      this.props.nftService.equip(
        this.props.nftService.config.assetConstants!.assetTypeCode,
        nft,
        this.props.equipTarget
      );
    } else {
      this.props.nftService.approve(this.props.equipTarget, this.props.nftService.config);
    }
  }

  render() {
    return (
      <React.Fragment>
        {this.props.nftService!.nfts.map(n => (
          <EquipmentRow
            key={n.name + n.index}
            onEquipClick={this.equip}
            isApproved={this.props.nftService.isApproved}
            nft={n}></EquipmentRow>
        ))}
      </React.Fragment>
    );
  }
}

interface IEquipmentRowProps {
  nft?: INftDetails;
  isApproved: boolean;
  onEquipClick: any;
}

class EquipmentRow extends Component<IEquipmentRowProps> {
  constructor(props: any) {
    super(props);

    this.equip = this.equip.bind(this);
  }

  equip() {
    this.props.onEquipClick(this.props.nft);
  }

  render() {
    return (
      <tr>
        <td>
          <div className="flex">
            <div className="tLogo">
              <img alt={this.props.nft!.name} className="token" src={this.props.nft!.image} />
            </div>
            <div>
              <div className="cell_PrimaryText">{this.props.nft!.name}</div>
            </div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">Description</div>
            <div className="cell_PrimaryText">{this.props.nft!.description}</div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">Attributes</div>
            {this.props.nft!.attributes.map(a => {
              return (
                <div className="cell_SecondaryText" key={a.id}>
                  {a.trait_type} : {a.value}
                </div>
              );
            })}
          </div>
        </td>
        <td>
          <div>
            <div className="cell_Action is-pulled-right">
              <button className="button is-success" onClick={this.equip}>
                {this.props.isApproved && <span>Equip</span>}
                {!this.props.isApproved && <span>Approve</span>}
              </button>
            </div>
          </div>
        </td>
      </tr>
    );
  }
}

export default NonFungibleAssets;
