export interface INftAttribute {
  id: number;
  display_type: string;
  trait_type: string;
  value: number;
}

export interface INftDetails {
  index: number;
  attributes: INftAttribute[];
  description: string;
  image: string;
  name: string;
}

export async function loadNftDetailsFromUri(tokenUri: string, index: number): Promise<INftDetails> {
  const tokenResponse = await fetch(tokenUri.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/'));
  var nft = (await tokenResponse.json()) as INftDetails;
  nft.image = nft.image.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/');
  nft.index = index;
  return nft;
}
